@import url("https://fonts.googleapis.com/css2?family=Signika:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,700&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}
.todolist_wrap::-webkit-scrollbar {
  display: none;
}
div {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
input {
  outline: none;
  box-shadow: none;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  width: 90%;
  border: none;
  top: 0;
  font-size: 16px;
}
.bg {
  width: 100%;
  height: 100vh;
  background-color: #e2f1fb;
}
.pd {
  padding: 1px;
}

.todolist_wrap {
  width: 500px;
  margin: auto;
  height: 500px;
  margin-top: calc(50vh - 250px);
  background-color: #ffffff;
  box-shadow: 10px 10px 4px #c1c0b9;
  padding: 50px;
  overflow-y: scroll;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.left_title {
  display: flex;
  align-items: center;
}
.left_title h4 {
  font-family: "Signika", sans-serif;
  font-size: 40px;
  padding: 0 5px 0 0;
}
.right_title h4 {
  font-size: 28px;
  font-weight: 700;
  font-style: italic;
  color: #b9f1f7;
}

.input {
  width: 100%;
  border-bottom: 1px solid #e2f1fb;
  position: relative;
  padding: 0 0 5px 0;
  margin-top: 20px;
}

.icon {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 15px;

  text-align: center;
  background-color: #50e3a4;

  color: white;
  border-radius: 50px;
}
.left_sub {
  font-size: 14px;
}

.list {
  padding: 12px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
  align-items: center;
}
.content_wrap {
  margin-top: 20px;
}
.sub {
  display: flex;
  justify-content: space-between;
  margin: 15px 0;
  align-items: center;
}
.sub > button {
  padding: 2px 0px;
  border: none;
  border-radius: 5px;
  padding: 4px 6px;
  color: white;
  background-color: #ff0000;
}
.sub > h2 {
  font-size: 18px;
  padding: 0px 0;
  color: black;
  font-weight: 700;
}
.check {
  background-color: #e1e1e1;
  padding: 0 2px;
  font-size: 13px;
  border-radius: 5px;
}
.check_icon {
  color: white;
}
.star {
  font-size: 15px;
  margin-right: 10px;
}
.star_icon {
  color: #e1e1e1;
}

.minus {
  font-size: 15px;
  text-align: center;

  color: white;
  border-radius: 50px;
  padding: 0 2px;
}
.minus_icon {
  color: tomato;
}

.check_end {
  background-color: #50e3a4;
  color: white;
}
.check_end2 {
  background-color: #c1c0b9;
  color: white;
}
.star_end {
  color: yellow;
}

.noline {
  text-decoration: line-through;
}

.content {
  flex-grow: 1;
  margin-left: 20px;
}
